import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { HIGHLIGHT_SUBJECT_KEY } from '../constants';
import { highlightSubject } from '../actions';

function useHighlight(controller: Controller, subjectId: string): [boolean, (value: boolean) => void] {
  const highlight = !!subjectId && subjectId === controller.value.data[HIGHLIGHT_SUBJECT_KEY];

  const setHighlight = React.useCallback((value: boolean) => {
    controller.run('onAction', highlightSubject({
      subjectId,
      highlight: value,
    }));
  }, [controller, subjectId]);

  return [highlight, setHighlight];
}

export default useHighlight;
